<template>
    <div>
        <mavon-editor
            v-model="content"
            ref="md"
            :ishljs = "true"
            @change="onchange"
            @imgAdd="imgAdd"
            style="min-height: 800px">
        </mavon-editor>
    </div>
</template>

<script>
    import { mavonEditor } from 'mavon-editor'
    import 'mavon-editor/dist/css/index.css'
    export default {
        name: "MDEditor",
        components:{
            mavonEditor,
        },

        data(){
            return{
                content:'',
                html:'',
            }
        },

        methods:{
            onchange(value, render){
                this.html = render
            },

            imgAdd(pos, file){
                let data = new FormData()
                data.append("file", file)
                data.append("type", "blog")
                let url = '/MD/uploadimg'
                this.putRequest(url, data).then(resp => {
                    if(resp){
                        alert(JSON.stringify(resp))
                        this.$refs.md.$img2Url(pos, this.$StaticImage + resp.obj)
                    }
                })
            },
        }
    }
</script>

