<template lang="html">
    <div class="blog">
        <nav-header @shareTags='' @shareOne=''></nav-header>

        <div class="headpic">
            <div class="container headtitle">
                <div class="title">
                    <h1 href="/blog">{{user.nickname}}</h1>
                </div>
            </div>
        </div>

        <div class="container content">
            <div class="list">
                <div class="main">
                    <article-list></article-list>
                </div>
                <div class="side">
                    <side-section>
                        <div slot="sidecontent">
                            <div class="pres">
                                <div class="pic">
                                    <el-avatar :size="100" :fit="'fill'" :src="this.$StaticImage + user.profile" :key="this.$StaticImage + user.profile" @error="imgErrorHandler"></el-avatar>
                                </div>
                                <div class="detail">
                                    <span class="item-info">
                                        <span class="item-title">昵称:{{user.nickname}}</span>
                                        <span class="item-desc">简介:{{user.sysnopsis}}</span>
                                    </span>
                                </div>
                            </div>
                        </div>
                    </side-section>

                    <side-section style="margin-top:30px">
                        <div slot="sidecontent" style="text-align: center">
                            <el-button @click="InfoDrawerOut">个人信息</el-button>
                            <el-button @click="WriteArticle">写博客</el-button>
                            <el-button @click="Discover">发现</el-button>
                        </div>
                    </side-section>

                    <side-section>
                        <div slot="sidecontent">
                            <li class="message-title">社交</li>
                            <div class="social" v-for="item in social">
                                <a target="_blank" :href="'https://' + item.address">
                                    <i :class="item.icon" style="color:#ff001c;margin-left: 20px;">
                                    </i>
                                </a>
                            </div>
                        </div>
                    </side-section>

                    <side-section>
                        <div slot="sidecontent">
                            <li class="message-title">标签</li>
                            <div class="tags">
                            </div>
                        </div>
                    </side-section>

                </div>
            </div>
        </div>

        <scroll-top></scroll-top>
        <nav-footer></nav-footer>

        <el-drawer
            title="发现朋友"
            :visible.sync="drawer"
            direction="rtl">
            <el-button round @click="Discover">刷新<i class="fa fa-refresh" aria-hidden="true"></i></el-button>
            <el-table :data="friends" v-if="friends">
                <template slot="empty">
                    没有好友上线哦！
                    <div style="horiz-align: center">
                        <el-image :src="this.$StaticImage + '/images/no-friend.png'" style="height: 70px;width: 60px"></el-image>
                    </div>
                </template>
                <el-table-column property="nickname" label="昵称"></el-table-column>
                <el-table-column label="操作" align="center" min-width="100px">
                    <template slot-scope="scope">
                        <el-dropdown>
                            <el-button type="primary">
                                邀请<i class="el-icon-arrow-down el-icon--right"></i>
                            </el-button>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item v-for="item in gamelist" :key="item.id" @click.native="InviteGoBang(scope.row, item)">{{item.name}}</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </template>
                </el-table-column>
            </el-table>
        </el-drawer>

        <el-dialog title="写博客" :visible.sync="dialogWriteVisible" custom-class="writedialog">
            <span>标题:</span>
            <el-input v-model="editblog.title" placeholder="请输入标题" style="margin-bottom: 20px"></el-input>
            <span>简介:</span>
            <el-input v-model="editblog.subtitle" placeholder="请输入简介" style="margin-bottom: 20px" @blur="getTagsByTitle"></el-input>
            <div>
                <span>标签</span>
                <el-tag v-for="item in tags" :key="item" closable @close="handleCloseTag(item)" style="margin-left: 20px">{{item.tag}}</el-tag>
                <el-input
                        class="input-new-tag"
                        v-if="inputVisible"
                        v-model="inputValue"
                        ref="saveTagInput"
                        size="small"
                        @keyup.enter.native="handleInputConfirm"
                        @blur="handleInputConfirm">
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
            </div>
            <div style="margin-top:20px">
                <span>内容:</span>
                <MDEditor v-model="editblog.content" ref="md"></MDEditor>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogWriteVisible = false">取 消</el-button>
                <el-button type="primary" @click="submitArticle">确 定</el-button>
            </span>
        </el-dialog>

        <Selfinfo :infodrawer="infodrawer" @func="InfoToggle"></Selfinfo>

    </div>
</template>

<script>
    import NavHeader from '@/components/Header.vue'
    import NavFooter from '@/components/Footer.vue'
    import articleList from '@/components/articleList.vue'
    import sideSection from '@/components/sideSection.vue'
    import scrollTop from '@/components/scrollTop.vue'
    import MDEditor from "@/components/MDEditor";
    import Selfinfo from "@/components/Selfinfo";
    import {Message} from 'element-ui'
    import {MessageBox} from 'element-ui'
    /*
    code:   100 断开服务器
            101 连接服务器
    */
    export default {
        name: 'Blog',
        components: {
            Selfinfo,
            MDEditor,
            NavHeader,
            NavFooter,
            articleList,
            sideSection,
            scrollTop
        },
        data() {
            return {
                gamelist:[],
                tags: [],
                tag: '',
                user: {},
                drawer: false,
                friends:[],
                param:{},
                dialogWriteVisible:false,
                editblog:{
                    title:'',
                    subtitle:'',
                    content:'',
                },
                inputVisible: false,
                inputValue: '',
                infodrawer:false,
                social:[{
                    'icon':'fa fa-weibo',
                    'address':'wwww.baidu.com',
                }],
            }
        },

        created(){
            this.EmptyUser()
            this.getUserInfo()
        },

        mounted() {
            this.getGameList()
        },

        methods: {
            JumpToSomeWhere(item){
                alert(JSON.stringify(item))
            },

            getUserInfo(){
                // alert("INFO")
                let username = window.sessionStorage.getItem("user")
                if(username == null){
                    window.sessionStorage.removeItem("user")
                    this.$router.push("/")
                    return ;
                }
                let url = "/user/?username=" + username
                this.getRequest(url).then(resp => {
                    if(resp) {
                        this.user = resp
                        // alert(JSON.stringify(resp))
                        this.setParam(101, {"id": this.user.username})
                        this.$ws.dispatch('WEBSOCKET_INIT', {"url": this.$WebSocketUrl, "params": this.param})
                    }
                })
            },

            getGameList(){
                let url = "/game/list"
                this.getRequest(url).then(resp => {
                    if(resp){
                        // alert(JSON.stringify(resp))
                        this.gamelist = resp.data
                    }
                })
            },

            EmptyUser(){
                this.user = {
                    nickname:'尚未登录',
                    sysnopsis:'暂无',
                    profile:''
                }
            },

            Discover(){
                this.drawer = true
                let url = '/socket/getOnlinePlayers'
                this.getRequest(url).then(resp => {
                    if(resp){
                        let data = []
                        data = resp.data
                        for(let i = 0;i < data.length;i ++){
                            if(data[i].username == this.user.username){
                                data.splice(i,1);
                                break;
                            }
                        }
                        this.friends = data
                    }
                })
            },

            WriteArticle(){
                this.dialogWriteVisible = true;
            },

            submitArticle(){
                this.editblog.content = this.$refs.md.content
                this.editblog['html'] = this.$refs.md.html
                this.editblog['author'] = this.user.username
                this.editblog['tags'] = this.tags
                // alert(JSON.stringify(this.tags))
                let url = '/article/add'
                let param = this.editblog
                this.postRequest(url, param).then(resp => {
                    this.dialogWriteVisible = false
                })
            },

            InviteGoBang(friend, game){
                if(game.name == '你画我猜'){
                    this.$notify({
                        title: '警告',
                        message: '功能改在开发当中哦',
                        type: 'warning'
                    });
                    return
                }
                let url = "/socket/create?username=" + friend.username
                this.postRequest(url, game).then(resp => {
                    alert(JSON.stringify(resp))
                    let rid = resp.msg
                    this.$router.push(game.path + "?rid=" + rid)
                })
            },

            setParam(code, data){
                this.EmptyParam()
                this.param.code = code
                for(let i in data){
                    this.param[i] = data[i]
                }
            },

            isJSON(str){
                if(typeof str == 'string'){
                    try{
                        let obj = JSON.parse(str)
                        if(typeof obj == 'object' && obj){
                            return true
                        }
                        else{
                            return false
                        }
                    }catch (e) {
                        console.log('error：'+str+'!!!'+e);
                        return false;
                    }
                }
                else{
                    return false
                }
            },

            EmptyParam(){
                this.param = {}
            },

            handleCloseTag(tag){
                this.tags.splice(this.tags.indexOf(tag, 1))
            },

            showInput() {
                this.inputVisible = true;
                this.$nextTick(_ => {
                    this.$refs.saveTagInput.$refs.input.focus();
                });
            },

            handleInputConfirm() {
                let inputValue = this.inputValue;
                if (inputValue) {
                    let ntag = {'tag' : inputValue, 'id':-1}
                    this.tags.push(ntag);
                }
                this.inputVisible = false;
                this.inputValue = '';
            },

            getTagsByTitle(){
                let title = this.editblog.title + this.editblog.subtitle
                if(title != '' && title){
                    let url = '/tag/getlist?&title=' + title
                    this.putRequest(url).then(resp => {
                        // alert(JSON.stringify(resp))
                        if(resp){
                            this.tags = resp.data
                        }
                    })
                }
            },

            imgErrorHandler(){
                return true
            },

            InfoDrawerOut(){
                this.infodrawer = true
            },

            InfoToggle(val){
                this.infodrawer = false
            }

        },

        computed: {
            alertCon(){
                return this.$ws.getters.onEvent('string')
            }
        },
        watch:{
            alertCon : function (a, b) {
                if(a != b && a){
                    console.log(a)
                    // alert(a)
                    if(this.isJSON(a)){
                        let data = JSON.parse(a)
                        if(data.code == 102){
                            let game = data.game
                            MessageBox.confirm("有朋友邀请你进行一场" + game.name +"，是否接受？",
                                {confirmButtonText:'确定', cancelButtonText:'拒绝', type:'info'}).then(() => {
                                this.$router.push(game.path + "?rid=" + data.rid)
                            }).catch(() => {
                                Message.info("你已经拒绝对局")
                            })
                        }
                    }
                }
            }
        }
    }
</script>

<style>
    body {
        background-color: #f4f4f4;
        font-family: 'Open Sans', 'Helvetica Neue',"Microsoft YaHei",'Hiragino Sans GB', 'LiHei Pro', Arial, sans-serif;
    }

    .secondary h3 {
        margin: 0 0 15px 0;
        font-size: 16px;
        font-weight: 500;
        line-height: 1;
    }

    .secondary h3 .cat-desc {
        display: block;
        margin: 5px 0 0 0;
        font-size: 14px;
        font-weight: normal;
        color: #999;
    }

    .pres {
        position: relative;
        line-height: 1.5;
        font-size: 14px;
        line-height: 1.5;
        /*background-color: #24292e;*/
        border-radius: 3px;
        margin-bottom: 15px;
    }

    div.pic {
        top: 3px;
        margin-left: 3px;
        left: 0;
        z-index: auto;
        position: absolute;
        height: 100px;
        width: 100px;
    }

    div.pic img {
        height: 100px;
        width: 100px;
        display: inline-block;
        overflow: hidden;
        line-height: 1;
        vertical-align: middle;
        border-radius: 8px;
    }

    .social {
        height:50px;
        padding-top: 10px;
        padding-left: 10px;
    }

    .detail {
        margin: 0 0 0 5px;
        padding: 0 0 0 110px;
        display: block;
        margin: 0 0 0 5px;
        font-weight: 500;
        text-decoration: none;
    }

    .item-info {
        width: calc(100% - 40%);
        line-height: 1.5;
        float: none;
    }

    .item-title {
        margin: 0;
        font-size: 20px;
        font-weight: 500;
        color: #3a8bbb;
        display: block;
    }

    .item-desc {
        font-size: 15px;
        font-weight: normal;
        color: #999;
        display: block;
    }

    .social a {
        display: inline-block;
        margin-bottom: 7px;
        text-decoration: none;
    }

    .social a  i {
        font-size: 22px;
        /*width: 30px;
        height: 30px;*/
        width: 38px;
        line-height: 38px;
        border-radius: 50px;
        color: #fff;
        background-color: rgba(0, 0, 0, .1);
        text-align: center;
    }

    /*.fa {*/
    /*    margin: 0 0.2em;*/
    /*}*/

    .message-title {
        border-bottom: 2px solid rgba(139, 139, 139, 0.5);
        padding: 0 10px;
        color: rgb(139, 139, 139);
        font-size: 12px;
        font-weight: 700;
        line-height: 25px;
    }

    .message-item {
        border-bottom: 1px dashed rgba(139, 139, 139, 0.4);
    }

    .message-from {
        height: 45px;
        padding: 8px 8px 0 57px;
        display: block;
        line-height: 14px;
        position: relative;
    }

    .message-avatar img {
        position: absolute;
        left: 10px;
        top: 8px;
        border-radius: 5px;
        display: block;
        height: 37px;
        width: 37px;
    }

    .l_title {
        color: #778087;
        font-weight: 700;
        padding: 1px 0 0;
        line-height: 17px;
        font-size: 14px;
    }

    .l_message {
        color: #778087;
        display: block;
        font-weight: 400;
        line-height: 17px;
        font-size: 13px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .tags {
        flex-flow: row wrap;
        display: flex;
        padding-top: 20px;
    }

    .tags .tags-item {
        margin: 0 16px 16px 0;
        padding: 0 15px;
        height: 30px;
        line-height: 30px;
        max-width: 100%;
        background: rgba(0, 0, 0, .06);
        color: #666768;
        border-radius: 15px;
        text-align: center;
        font-size: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        display: block;
    }

    .header-upload-btn{
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
        /* 通过定位把input放在img标签上面，通过不透明度隐藏 */
    }

    .writedialog{
        width: 90%;
        height: 90%;
        overflow-y: auto;
        display: flex;
        flex-direction: column;
        margin:0 !important;
        position:absolute;
        top:50%;
        left:50%;
        transform:translate(-50%,-50%);
        flex:1;
        overflow: auto;
    }

    .button-new-tag {
        margin-left: 10px;
        height: 32px;
        line-height: 30px;
        padding-top: 0;
        padding-bottom: 0;
    }
    .input-new-tag {
        width: 90px;
        margin-left: 10px;
        vertical-align: bottom;
    }

</style>
