<template>
    <div>
        <el-drawer
                class="my-el-drawer"
            :title="`${olduserinfo.nickname}的个人信息`"
            :direction="'ltr'"
            :visible.sync="drawer"
            :before-close="handleClose"
            >
            <div class="info-section">
                <div class="info-title">
                    <span>头像(点击切换):</span>
                </div>
                <div style="text-align: center">
                    <el-upload
                            class="avatar-uploader"
                            action='/user/profile'
                            accept="image/*"
                            :data="dataForm"
                            :on-success="handleAvatarSuccess"
                            :before-upload="beforeAvatarUpload">
                        <img v-if="userinfo.profile" :src="this.$StaticImage + userinfo.profile" class="avatar">
                        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </div>
            </div>
            <div class="info-section">
                <div class="info-title">
                    <span>昵称</span>
                </div>
                <div style="text-align: center">
                    <el-input
                            v-model="userinfo.nickname"
                            class="info-input">
                    </el-input>
                </div>
            </div>

            <div class="info-section">
                <div class="info-title">
                    <span>用户名</span>
                </div>
                <div style="text-align: center">
                    <el-input
                            v-model="userinfo.username"
                            :disabled="true"
                            class="info-input">
                    </el-input>
                </div>
            </div>
            <div class="info-section">
                <div class="info-title">
                    <span>密码</span>
                </div>
                <div style="text-align: center">
                    <el-input
                            placeholder="密码暂时不支持更换"
                            :disabled="true"
                            show-password
                            class="info-input">
                    </el-input>
                </div>
            </div>
            <div class="info-section">
                <div class="info-title">
                    <span>简介</span>
                </div>
                <div style="text-align: center">
                    <el-input
                            v-model="userinfo.sysnopsis"
                            class="info-input">
                    </el-input>
                </div>
            </div>
        </el-drawer>
    </div>
</template>

<script>
    import {Message} from 'element-ui'
    import {MessageBox} from 'element-ui'
    export default {
        name: "Selfinfo",
        props:{
            'infodrawer':Boolean,
        },
        data(){
            return{
                drawer:this.infodrawer,
                userinfo:{},
                olduserinfo:{},
                dataForm:{},
            }
        },

        created(){
            this.getUserInfo()
        },

        methods:{
            handleClose(){
                if(!this.isObjectEqual(this.olduserinfo, this.userinfo)){
                    // alert("yes")
                    MessageBox.confirm("检测到你的基本信息改动，是否保存",
                        {confirmButtonText:'确定', cancelButtonText:'拒绝', type:'info'}).then(() => {
                        let url = '/user/updateInfo'
                        let data = this.userinfo
                        this.putRequest(url, data).then(resp => {
                            location.reload()
                        })
                    }).catch(() => {
                        this.userinfo = this.getObjectCopy(this.olduserinfo)
                    })
                }
                this.drawer = false
                this.$emit('func', this.drawer)
            },

            getUserInfo(){
                let username = window.sessionStorage.getItem("user")
                if(username == null){
                    window.sessionStorage.removeItem("user")
                    this.$router.push("/")
                    return ;
                }
                let url = "/user/?username=" + username
                this.getRequest(url).then(resp => {
                    this.userinfo = resp
                    this.olduserinfo = this.getObjectCopy(resp)
                    // alert(JSON.stringify(resp))
                })
            },

            beforeAvatarUpload(file) {
                const isLt2M = file.size / 1024 / 1024 < 2;
                this.dataForm['user'] = this.userinfo.username
                this.dataForm['profile'] = this.userinfo.profile
                if (!isLt2M) {
                    Message.error('上传头像图片大小不能超过 2MB!');
                }
                return isLt2M;
            },

            getObjectCopy(data){
                let obj = {}
                for(let i in data){
                    obj[i] = data[i]
                }
                return obj
            },

            isObjectEqual(o1, o2){
                for(let i in o1){
                    if(o1[i] != o2[i])
                        return false
                }
                return true
            },

            handleAvatarSuccess(res, file) {
            },
        },

        watch:{
            infodrawer: function (a,b) {
                if(a != b && a){
                    this.drawer = a
                }
            }
        }
    }
</script>

<style scoped>
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
        border-color: #409EFF;
    }
    .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        text-align: center;
    }
    .avatar {
        width: 100px;
        height: 100px;
        display: block;
    }

    .info-title{
        margin-left: 30px;
    }
    .info-section{
        margin-top: 50px;
    }

    .info-input{
        width: 70%;
        margin-top: 10px;
    }
</style>
